body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.gray-line {
    width: 100%;
    background: #dedede;
}

.displayNone{
    display: none;
}

.bindParent {
    width: 100%;
    height: 100%;
    background-image: url(/wxService/static/media/bg_bind_phone.ddff5923.jpg);
    background-size: 100% 100%;
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
    padding: 0 50px;
}

.uasIcon {
    width: 90px;
    height: 30px;
    margin-top: 80px;
}

.phoneLayout {
    width: 100%;
    height: 156px;
    background-color: rgba(255, 255, 255, 0.25);
    display: flex;
    flex-direction: column;
    margin: 50px auto;
    border-radius: 30px 0 30px 0;
    padding: 0 25px;
}

.phoneCaption {
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #FFFFFF;
    letter-spacing: 1.48px;
    margin-top: 25px;
}

.phoneInput {
    width: 100%;
    margin-top: 20px;
    height: 35px;
    border: 1px solid #FFFFFF;
    border-radius: 3px;
    background: transparent;
    color: white;
    padding: 8px;
}

.phoneInput::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    color: white;
}

.phoneInput:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color: white;
}

.phoneInput::-moz-placeholder { /* Mozilla Firefox 19+ */
    color: white;
}

.phoneInput:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: white;
}

.bindButton {
    background: rgba(252, 252, 252, 0.6);
    font-size: 14px;
    color: #005BFF;
    letter-spacing: 8px;
    height: 40px;
    text-align: center;
    line-height: 42px;
}

.bindButton:active {
    background: rgba(252, 252, 252, 0.8);
}
.root {
    width: 100%;
    height: 100%;
    background: white;
    display: flex;
    flex-direction: column;
}

.content {
    width: 100%;
    flex: 1 1;
    overflow-y: scroll;
    overflow-x: hidden;
}

.content::-webkit-scrollbar {
    display: none;
}

.bottomMenu {
    width: 100%;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
}

.editLayout {
    width: 100%;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    display: flex;
    padding: 8px;
    font-size: 12px;
}

.editInput {
    flex: 1 1;
    min-height: 30px;
    max-height: 50px;
    margin: 0 10px;
}

.menuParent {
    width: 100%;
    display: flex;
    padding: 8px;
    justify-content: center;
    align-items: center;
}

.fastWords {
    width: 24px;
    height: 24px;
    text-align: center;
    line-height: 20px;
    background: red;
    border-radius: 20px;
    padding: 2px;
    color: white;
    background: gray;
    font-size: 12px;
}

.fastItem {
    height: 38px;
    text-align: center;
    line-height: 35px;
    border-bottom: 1px solid #ccc;
}

.menuItem {
    flex: 1 1;
    height: 28px;
    text-align: center;
    line-height: 30px;
    color: deepskyblue;
    background: white;
}

.menuItem:active {
    color: white;
    background: deepskyblue;
}

.headerLayout {
    width: 100%;
    padding: 10px;
    display: flex;
    align-items: center;
}

.headerImage {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    border: red solid 1px;
}

.headerText {
    font-size: 14px;
    color: black;
    margin-left: 10px;
}

.tableTitle {
    width: 100%;
    padding: 10px 6px;
    color: dodgerblue;
    font-size: 14px;
}

.approval_tab_parent {
    width: 100%;
    display: flex;
    align-items: center;
    font-size: 13px;
}

.approval_tab_normal {
    flex: 1 1;
    height: 40px;
    color: #000;
    border-bottom: 1px solid #ccc;
    text-align: center;
    line-height: 40px;
}

.approval_tab_selected {
    flex: 1 1;
    height: 40px;
    color: deepskyblue;
    border-bottom: 1px solid deepskyblue;
    text-align: center;
    line-height: 40px;
}

.ant-spin {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


div {
    margin: 0px;
    padding: 0px;
}

.points-parent {
    width: 100%;
    display: flex;
    box-sizing: border-box;
    flex-direction: row;
    align-items: center;
    background: white;
    border-bottom: 1px solid #ccc;
    padding: 3px 0px;
}

.points-caption {
    width: 100px;
    color: #999999;
    font-size: 12px;
    margin-left: 6px;
}

.points-value {
    flex: 1 1;
    height: 26px;
    text-align: right;
    line-height: 26px;
    margin-left: 6px;
    color: #333333;
    font-size: 13px;
}

.value-input, .date-input {
    width: 100%;
    background: none;
    border: none;
    text-align: right;
}

.value-input .ant-calendar-picker-input {
    width: 100%;
    color: #333333;
    font-size: 13px;
    padding-left: 12px;
}

.date-input .ant-calendar-picker-input {
    width: 100%;
    background: none;
    border: none;
    text-align: right;
    color: #333333;
    font-size: 13px;
}

.date-input .anticon {
    display: none;
}

.selectItem {
    height: 38px;
    text-align: center;
    line-height: 35px;
    border-bottom: 1px solid #ccc;
}
.table-parent {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    background: white;
    padding: 4px 10px;
    font-size: 13px;
}

.table-caption {
    width: 100px;
    color: #999999;
}

.table-value {
    flex: 1 1;
    height: 24px;
    line-height: 24px;
    color: #333333;
}

.value-input {
    height: 24px;
    line-height: 24px;
    color: #333333;
    width: 100%;
    margin-right: 10px;
}

DatePicker {
    margin: 0px;
    padding: 0px;
}

.selectItem {
    height: 38px;
    text-align: center;
    line-height: 35px;
    border-bottom: 1px solid #ccc;
}

.parent {
    width: 100%;
    height: 72px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-right: 10px;
}

.dateLayout {
    width: 54px;
    display: flex;
    flex-direction: column;
    justify-content: right;
    align-items: center;
    padding-top: 10px;
}

.dateItem, .timeItem {
    font-size: 13px;
}

.arrowLayout {
    display: flex;
    height: 100%;
    flex-direction: column;
    align-items: center;
}

.arrowLine {
    flex: 1 1;
    width: 1px;
    background: #cccccc;
}

.headerImg {
    width: 35px;
    height: 35px;
    margin-left: 10px;
}

.contentLayout {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1 1;
    margin-left: 10px;
}

.nameItem {
    color: #999999;
    font-size: 13px;
}

.opinionItem {
    color: #999999;
    font-size: 12px;
}

.stateItem {
    color: #999999;
    margin-bottom: 20px;
    font-size: 12px;
}

