.bindParent {
    width: 100%;
    height: 100%;
    background-image: url("./bg_bind_phone.jpg");
    background-size: 100% 100%;
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
    padding: 0 50px;
}

.uasIcon {
    width: 90px;
    height: 30px;
    margin-top: 80px;
}

.phoneLayout {
    width: 100%;
    height: 156px;
    background-color: rgba(255, 255, 255, 0.25);
    display: flex;
    flex-direction: column;
    margin: 50px auto;
    border-radius: 30px 0 30px 0;
    padding: 0 25px;
}

.phoneCaption {
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #FFFFFF;
    letter-spacing: 1.48px;
    margin-top: 25px;
}

.phoneInput {
    width: 100%;
    margin-top: 20px;
    height: 35px;
    border: 1px solid #FFFFFF;
    border-radius: 3px;
    background: transparent;
    color: white;
    padding: 8px;
}

.phoneInput::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    color: white;
}

.phoneInput:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color: white;
}

.phoneInput::-moz-placeholder { /* Mozilla Firefox 19+ */
    color: white;
}

.phoneInput:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: white;
}

.bindButton {
    background: rgba(252, 252, 252, 0.6);
    font-size: 14px;
    color: #005BFF;
    letter-spacing: 8px;
    height: 40px;
    text-align: center;
    line-height: 42px;
}

.bindButton:active {
    background: rgba(252, 252, 252, 0.8);
}