.root {
    width: 100%;
    height: 100%;
    background: white;
    display: flex;
    flex-direction: column;
}

.content {
    width: 100%;
    flex: 1;
    overflow-y: scroll;
    overflow-x: hidden;
}

.content::-webkit-scrollbar {
    display: none;
}

.bottomMenu {
    width: 100%;
    height: max-content;
}

.editLayout {
    width: 100%;
    height: max-content;
    display: flex;
    padding: 8px;
    font-size: 12px;
}

.editInput {
    flex: 1;
    min-height: 30px;
    max-height: 50px;
    margin: 0 10px;
}

.menuParent {
    width: 100%;
    display: flex;
    padding: 8px;
    justify-content: center;
    align-items: center;
}

.fastWords {
    width: 24px;
    height: 24px;
    text-align: center;
    line-height: 20px;
    background: red;
    border-radius: 20px;
    padding: 2px;
    color: white;
    background: gray;
    font-size: 12px;
}

.fastItem {
    height: 38px;
    text-align: center;
    line-height: 35px;
    border-bottom: 1px solid #ccc;
}

.menuItem {
    flex: 1;
    height: 28px;
    text-align: center;
    line-height: 30px;
    color: deepskyblue;
    background: white;
}

.menuItem:active {
    color: white;
    background: deepskyblue;
}

.headerLayout {
    width: 100%;
    padding: 10px;
    display: flex;
    align-items: center;
}

.headerImage {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    border: red solid 1px;
}

.headerText {
    font-size: 14px;
    color: black;
    margin-left: 10px;
}

.tableTitle {
    width: 100%;
    padding: 10px 6px;
    color: dodgerblue;
    font-size: 14px;
}

.approval_tab_parent {
    width: 100%;
    display: flex;
    align-items: center;
    font-size: 13px;
}

.approval_tab_normal {
    flex: 1;
    height: 40px;
    color: #000;
    border-bottom: 1px solid #ccc;
    text-align: center;
    line-height: 40px;
}

.approval_tab_selected {
    flex: 1;
    height: 40px;
    color: deepskyblue;
    border-bottom: 1px solid deepskyblue;
    text-align: center;
    line-height: 40px;
}

.ant-spin {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

